.AboutMe-container{
    height:100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

/* Styles for large desktop screens (1441px and above) */
@media (min-width: 1441px) {
    .AboutMe-contentContainer{
        width:60%;
        height:80vh;
        display:flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }

    .AboutMe-titleTextContainer{
        height:4vh;
        margin-bottom: 2vh;
        font-size:2rem;
        font-family: "Montserrat Bold";
        text-decoration: underline;
    }

    .AboutMe-pictureAndInformationContainer{
        width:100%;
        display:flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
    }

    .AboutMe-pictureContainer{
        width:100%;
        height: 45vh;
    }

    .AboutMe-picture{
        width:100%;
        height:45vh;
        object-fit: contain;
    }

    .AboutMe-informationContainer{
        width:70%;
        height:35vh;
        font-size:1.15rem;
        display:flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
    }

}

/** Styles for medium to large laptop screens (1025px - 1440px) */
@media (min-width: 768px) and (max-width: 1440px) {
    .AboutMe-contentContainer{
        width:80%;
        height: 80vh;
        display:flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }

    .AboutMe-titleTextContainer{
        height:4vh;
        margin-bottom: 2vh;
        font-size:2rem;
        font-family: "Montserrat Bold";
        text-decoration: underline;
    }

    .AboutMe-pictureAndInformationContainer{
        width:100%;
        display:flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
    }

    .AboutMe-pictureContainer{
        width:100%;
        height: 30vh;
    }

    .AboutMe-informationContainer{
        width:70%;
        height:35vh;
        font-size:1rem;
        display:flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
    }

    .AboutMe-picture{
        width:100%;
        height: 30vh;
        object-fit: contain;
    }
}