.HomeSectionMobile-container{
    width:100%;
    height:150vmin;
    display:flex; 
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
}

.HomeMobile-helloText{
    color:red;
    font-weight:500;
}

.HomeMobile-titleText{
    color:#B98D74;
    font-size:1.15rem;
}

.HomeMobile-nameText{
    font-size:1.5rem;
    font-family: "Montserrat Bold";
}

.HomeMobile-textContainer{
    display:flex;
    flex-direction:column;
    justify-content: flex-start;
    align-items: center;
}

.HomeMobile-introductionText{
    font-size:0.95rem;
}
.HomeMobile-profileInformationContainer{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    text-align:center;
    font-size:1.75rem;
}

.HomeMobile-profileIcon{
    font-size: 1.75rem;
    margin-right:1vmin;
}