.Skill-container{
    height:100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.Skill-React{
    color: #61DAFB;
}

.Skill-JavaScript{
    color:#F7DF1E;
}

.Skill-CSS{
    color: #1572B6;
}

.Skill-Java{
    color:#5382A1;
}

.Skill-Python{
    color:#3776AB;
}
.Skill-HTML5{
    color:#E44D26;
}

.Skill-NodeJs{
    color:#68A063;
}

.Skill-Git{
    color:#F34F29;
}

.Skill-AWS{
    color:#FF9900;
}

@media (min-width:1440px){
    .Skill-contentContainer{
        width:60%;
        height:80vh;
        display:flex;
        flex-direction: column;
        justify-content: flex-start;
        align-items: center;
    }
    
    .Skill-titleTextContainer{
        height:5%;
        width:100%; 
        display:flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        font-size:2rem;
        font-family: "Montserrat Bold";
        text-decoration: underline;
    }
    
    .Skill-cardsContainer{
        margin-top:1%;
        height:94%;
        width:100%;
        display: flex; 
        flex-direction: column; 
        justify-content: space-evenly;
        align-items:center;
    }

    .Skill-rowCard{
        width:100%;
        height:25%;
        display: flex; 
        flex-direction: row; 
        justify-content: space-evenly;
        align-items:center;
    }

    .Skill-cardItem{
        width:20%;
        height:100%;
        display: flex; 
        flex-direction: column; 
        justify-content: center;
        align-items:center;
        text-align: center;
        font-size:1.5rem;
    }

    .Skill-iconContainer{
        font-size:4rem;
        display: flex; 
        flex-direction: column; 
        justify-content: center;
        align-items:center;
        aspect-ratio: 1;
    }
    
    .Skill-iconTitle{
        font-size:1.5rem;
    }
}

@media (min-width: 768px) and (max-width: 1440px) {
    .Skill-contentContainer{
        width:60%;
        height:80vh;
        display:flex;
        flex-direction: column;
        justify-content: flex-start;
        align-items: center;
    }
    
    .Skill-titleTextContainer{
        height:5%;
        width:100%; 
        display:flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        font-size:2rem;
        font-family: "Montserrat SemiBold";
        text-decoration: underline;
    }
    
    .Skill-cardsContainer{
        margin-top:1%;
        height:94%;
        width:100%;
        display: flex; 
        flex-direction: column; 
        justify-content: space-evenly;
        align-items:center;
    }

    .Skill-rowCard{
        width:100%;
        height:25%;
        display: flex; 
        flex-direction: row; 
        justify-content: space-evenly;
        align-items:center;
    }

    .Skill-cardItem{
        width:20%;
        height:100%;
        display: flex; 
        flex-direction: column; 
        justify-content: center;
        align-items:center;
        text-align: center;
        font-size:1.5rem;
    }

    .Skill-iconContainer{
        font-size:2.75rem;
        display: flex; 
        flex-direction: column; 
        justify-content: center;
        align-items:center;
        aspect-ratio: 1;
    }
    
    .Skill-iconTitle{
        font-size:1rem;
    }
}