.Home-container{
    height:100vh;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.Home-textContainer{
    width:100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.Home-introductionContainer{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align:center;
}

.Home-helloText{
    color:red;
}

.Home-titleText{
    color:#B98D74;
}

.Home-profileInformationContainer{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    text-align:center;
}

.Home-linkedin:hover{
    color: #0077B5; 
    transition: color 0.3s ease-in-out;
    cursor:pointer;
}

.Home-twitter:hover{
    color: #1DA1F2; 
    transition: color 0.3s ease-in-out;
    cursor:pointer;
}

.Home-github:hover, .Home-resume:hover{
    color: #333; 
    transition: color 0.3s ease-in-out;
    cursor:pointer;
}

.Home-resumeContainer {
    height:100%;
}

/** Styles for tablet and small laptop screens (768px - 1024px) */
@media (min-width: 768px) and (max-width: 1024px) {
    .Home-nameText {
        font-size: 2.75rem;
        font-family: "Montserrat Bold";
    }
    .Home-helloText{
        font-size: 1rem;
    }
    .Home-titleText{
        font-size:1.25rem;
    }
    .Home-introductionContainer, .Home-profileInformationContainer{
        width:75%;
        height:100%;
    }
    .Home-profileIcon{
        font-size: 2.5rem;
        margin-right:18px;
    }
}

/** Styles for medium to large laptop screens (1025px - 1440px) */
@media (min-width: 1025px) and (max-width: 1440px) {
    .Home-nameText {
      font-size: 3rem;
      font-family: "Montserrat Bold";
    }
    .Home-helloText{
        font-size: 1.2rem;
    }
    .Home-titleText{
        font-size:1.5rem;
    }
    .Home-introductionContainer, .Home-profileInformationContainer{
        width:75%;
        height:100%;
    }
    .Home-profileIcon{
        font-size: 2.5rem;
        margin-right:16px;
    }
}

/* Styles for large desktop screens (1441px and above) */
@media (min-width: 1441px) {
    .Home-nameText {
        font-size: 3.125rem;
        font-family: "Montserrat Bold";
    }
    .Home-helloText{
        font-size: 1.5rem;
    }
    .Home-titleText{
        font-size:1.75rem;
    }
    .Home-introductionContainer, .Home-profileInformationContainer{
        width:75%;
        height:100%;
    }
    .Home-profileIcon{
        font-size: 2.5rem;
        margin-right:20px;
    }
}