.NavBar-container{
    height: 8%;
    z-index: 1;
}

.NavBar-sectionContainer{
    display:flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    height:100%;
    width:50%;
}

.NavBar-homeTitle:hover,
.NavBar-aboutMeTitle:hover,
.NavBar-projectTitle:hover,
.NavBar-skillTitle:hover,
.NavBar-contactTitle:hover {
  text-decoration: underline;
  font-size: 1.2em;
}

.NavBar-homeTitle,
.NavBar-aboutMeTitle,
.NavBar-projectTitle,
.NavBar-skillTitle,
.NavBar-contactTitle {
  transition: font-size 0.2s ease-in-out;
  cursor: pointer;
}

.NavBar-ActiveSection{
    color:red;
}

/** Styles for tablet and small/large laptop screens (768px - 1440px) */
@media (min-width: 768px) and (max-width: 1440px) {
    .NavBar-aboutMeTitleContainer, .NavBar-projectTitleContainer, .NavBar-skillTitleContainer, .NavBar-homeTitleContainer{
        margin-right: 7%;
    }
    .NavBar-aboutMeTitleContainer, .NavBar-projectTitleContainer, .NavBar-skillTitleContainer, .NavBar-homeTitleContainer, .NavBar-contactTitleContainer{
        height:100%;
        display:flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
    }
}

/* Styles for large desktop screens (1441px and above) */
@media (min-width: 1441px) {
    .NavBar-aboutMeTitleContainer, .NavBar-projectTitleContainer, .NavBar-skillTitleContainer, .NavBar-homeTitleContainer{
        margin-right: 4%;
    }
    .NavBar-aboutMeTitleContainer, .NavBar-projectTitleContainer, .NavBar-skillTitleContainer, .NavBar-homeTitleContainer, .NavBar-contactTitleContainer{
        height:100%;
        display:flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
    }
}