/** The outside container */
.Project-container{
    height:100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

/** The content container where we put all the cards in */
.Project-contentContainer{
    height:80vh;
    width:80%;
    display:flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
}

/** The back of the card */
.Project-backCardContainer{
    display:flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    position: absolute;
    backface-visibility: hidden;
    transform: rotateY(180deg);
    transition: all 0.6s ease 0s;
    border:1px solid black;
    color: #36454F;
    background-color:lightgray;
}

/** The description part in the back of the card */
.Project-backCardDescriptionContainer{
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    
}

/** The title of the description part in the back of the card (The name of the project) */
.Project-backCardDescriptionTitle{
    text-decoration: underline;
    font-family:"Faustina Bold";
}

/** The outer container of the button (most likely the remain of the card that is not desription) */
.Project-gitHubButtonOuterContainer{
    display:flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
}

/** The container of the button */
.Project-gitHubButtonContainer{
    display:flex;
    justify-content: center;
    align-items: center;
    border: 1px solid white;
    background-color: white;
}

/** The hover of the button */
.Project-gitHubButtonContainer:hover{
    color:white !important;
    border: 1px solid black;
    background-color: black;
    cursor:pointer;
}

/** In a card, there is a skill row at the bottom, we split into 2 rows maximum. */
.Project-rowSkillCard{
    display:flex;
    flex-direction:row;
    justify-content: space-evenly;
    align-items: center;
}

/** The container of the skill card inside the skill row */ 
.Project-skillCardContainer{
    text-align: center;
    display:flex;
    flex-direction:column;
}

/** There are two type of skill card, the skill card with normal width 30%, and extension has wider width */ 
.Project-skillCard, .Project-skillCardExtension{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    text-align: center;
    border:1px solid black;
}

/** The hover of the skill card */
.Project-skillCard:hover, .Project-skillCardExtension:hover{
    background-color: lightgray;
    color: #2C2C2C;
    cursor:pointer;
}

/** The card picture (front of the card) */
.Project-cardPicture{
    backface-visibility: hidden;
    position: absolute;
    object-fit: cover;
    border:1px solid black;
    z-index: 2;
    transform: rotateY(0deg); 
    transition: all 0.6s ease 0s;
}

/** The flip transform when user hover on the card */ 
.Project-cardContainer:hover .Project-cardPicture {
    transform: rotateY(180deg);
}
  
.Project-cardContainer:hover .Project-backCardContainer {
    transform: rotateY(0deg);
}

@media (min-width: 1441px) {
    .Project-titleTextContainer{
        font-size:2rem;
        text-decoration: underline;
        font-family: "Montserrat Bold";
        margin-bottom: 2vh;
    }
    
    .Project-displayCardsContainer{
        width:100%;
        height:100%; /** 74vh */
        display:flex;
        flex-direction:column;
        justify-content: flex-start;
        align-items: center;
    }

    .Project-row{
        width:80%;
        height:50%;
        display:flex;
        flex-direction:row;
        justify-content: space-evenly;
        align-items: center;
    }

    .Project-itemContainer{
        height: 90%;
        width: calc(0.8*37vh);
        margin-right:2%;
    }

    .Project-cardContainer{
        position:relative;
        height:80%;
        width: calc(0.8 * 37vh);
        border-radius: 15px;
        border:1px solid white;
        perspective: 1000px;
    }


    .Project-backCardContainer{
        height:100%;
        width:100%;
        border-radius: 15px;
        font-size: 1.15rem;
    }

    .Project-backCardDescriptionContainer{
        padding-left:1%;
        padding-right:1%;
        height:60%; 
    }

    .Project-backCardDescriptionTitle{
        font-size:1.2rem;
    }

    .Project-gitHubButtonOuterContainer{
        width:100%;
        height:40%;
    }

    .Project-gitHubButtonContainer{
        width:80%;
        height:35%;
        border-radius:10px;
    }

    .Project-cardPicture{
        width:100%;
        height:100%;
        border-radius: 15px;
    }

    .Project-skillCardContainer{
        width:100%;
        height:20%;
        font-size:1.15rem;
    }

    .Project-rowSkillCard{
        width:100%;
        height:50%;
    }

    .Project-skillCard{
        width:30%;
        height:80%;
        border-radius: 5px;
    }

    .Project-skillCardExtension{
        width:45%;
        height:80%;
        border-radius: 5px;
    }

}

