.ProjectMobile-container{
    width:100%;
    height:235vmin;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.ProjectMobile-titleTextContainer{
    font-size:1.5rem;
    font-family: "Montserrat Bold";
    text-decoration: underline;
    margin-bottom:5%;
}

.ProjectMobile-displayCardsContainer{
    height:80%;
    width:100%;
    display:flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
}

.ProjectMobile-row{
    margin-bottom:2%;
    width:90%;
    height:30%;
    display:flex;
    flex-direction:row;
    justify-content: space-evenly;
    align-items: center;
}
.ProjectMobile-lastRow{
    width:90%;
    height:30%;
    display:flex;
    flex-direction:row;
    justify-content: space-evenly;
    align-items: center;
}
.ProjectMobile-frontCardContainer{
    position:relative;
    height:30vmin;
    width:30vmin;
    border-radius: 15px;
    border:1px solid black;
    perspective: 1000px;
}

.ProjectMobile-cardPicture{
    height:30vmin;
    width:30vmin;
    object-fit: contain;
    border-radius: 15px;
}

.ProjectMobile-cardPictureFit{
    height:30vmin;
    width:30vmin;
    object-fit: cover;
    border-radius: 15px;
}

.ProjectMobile-itemContainer{
    height:92%;
    display:flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
}

.ProjectMobile-skillCardContainerTitle{
    height:15%;
    width:100%;
}

.ProjectMobile-rowSkillCardTitle{
    font-size:1rem;
    text-align: center;
    text-decoration: underline;
}

.ProjectMobile-rowSkillCard{
    font-size:0.9rem;
    text-align: center;
}