.Contact-container {
    height:100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.Contact-titleTextContainer{
    width:100%; 
    text-decoration: underline;
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-family: "Montserrat Bold";
}

.Contact-emailTextContainer{
    margin-top:0.5%;
    width:100%; 
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.Contact-URLContainer{
    margin-top:0.5%;
    width:100%; 
    display:flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.Contact-linkedin:hover{
    color: #0077B5; 
    transition: color 0.3s ease-in-out;
    cursor:pointer;
}

.Contact-twitter:hover{
    color: #1DA1F2; 
    transition: color 0.3s ease-in-out;
    cursor:pointer;
}

.Contact-github:hover{
    color: #333; 
    transition: color 0.3s ease-in-out;
    cursor:pointer;
}


.Contact-contentContainer{
    width:60%;
    height:80vh;
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.Contact-titleTextContainer{
    font-size:2rem;
}
.Contact-emailTextContainer{
    font-size: 1.5rem;
}
.Contact-profileIcon{
    font-size: 2.5rem;
    margin-right:20px;
}
