.AboutMeMobile-container{
    width:100%;
    height:235vmin;
    display:flex;
    flex-direction:column;
    justify-content: center;
    align-items: center;
}

.AboutMeMobile-pictureContainer{
    margin-top:2vmin;
    height:30%;
}

.AboutMeMobile-picture{
    height:100%;
    object-fit:contain;
}

.AboutMeMobile-informationContainer{
    width:95%;
    font-size:0.9rem;
    margin-top:5%;
    overflow:auto;
}

.AboutMeMobile-titleTextContainer{
    font-size:1.5rem;
    font-family: "Montserrat Bold";
    text-decoration: underline;
}