.SkillMobile-container{
    height:235vmin;
    width:100%;
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.SkillMobile-titleTextContainer{
    margin-bottom:5%;
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-size:1.5rem;
    font-family: "Montserrat Bold";
    text-decoration: underline;
}

.SkillMobile-displayCardsContainer{
    width:95%;
    height:80%;
    display:flex;
    flex-direction:column;
    justify-content: space-evenly;
    align-items: center;
    text-align: center;
}

.SkillMobile-rowCardContainer{
    width:100%;
    height:15%;
    display:flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items:center;
    margin-bottom:5%;
}

.SkillMobile-cardItem{
    width:30%;
    height:100%;
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-size:0.9rem;
}

.SkillMobile-iconContainer{
    font-size:1.75rem;
}

.SkillMobile-React{
    color: #61DAFB;
}

.SkillMobile-JavaScript{
    color:#F7DF1E;
}

.SkillMobile-Python{
    color:#3776AB;
}

.SkillMobile-HTML5{
    color:#E44D26;
}

.SkillMobile-CSS{
    color: #1572B6;
}

.SkillMobile-Java{
    color:#5382A1;
}

.SkillMobile-NodeJs{
    color:#68A063;
}

.SkillMobile-Git{
    color:#F34F29;
}

.SkillMobile-AWS{
    color:#FF9900;
}