@import url("./styles/fonts/Fonts.css");

body {
    font-family: "Faustina Regular";
    margin: 0px;
    overflow-x: auto;
    overflow-y: auto;
    min-height: 100vh;
}

/** Global Headings */
.heading1,
.heading2,
.heading3,
.heading4 {
    margin: 0px 0px;
    font-family: "Montserrat SemiBold";
}

/** Different Screen */

