    }
} */

/** Styles for small screens (321px - 767px) */
/* @media (min-width: 321px) and (max-width: 767px) {
    body {
      font-size: 16px;
    }
} */

/** Styles for tablet and small laptop screens (768px - 1024px) */
@media (min-width: 768px) and (max-width: 1024px) {
    body {
      font-size: 16px;
    }
}

/** Styles for medium to large laptop screens (1025px - 1440px) */
@media (min-width: 1025px) and (max-width: 1440px) {
    body {
      font-size: 18px;
    }
}

/* Styles for large desktop screens (1441px and above) */
@media (min-width: 1441px) {
    body {
      font-size: 20px;
    }
}