.ContactMobile-container{
    width:100%;
    height:100vmin;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.ContactMobile-titleTextContainer{
    font-size:1.5rem;
    font-family: "Montserrat Bold";
    text-decoration: underline;
    margin-bottom:1%;
}

.ContactMobile-emailTextContainer{
    margin-bottom:1%;
    font-size:1.25rem;  
}

.ContactMobile-profileIcon{
    margin-top:1%;
    font-size: 1.75rem;
    margin-right:15px;
}

.ContactMobile-messageContainer{
    margin-bottom: 1%;
    font-size:0.9rem; 
    text-align: center;
}